.App {
  text-align: center;
  background-color: #282c34;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
#learn{
  background-color: rgb(86, 160, 83);
  height: 100%;
  padding: .7rem;
  margin: 0px .7rem;
}


/****************************************************/
*{max-width: 100vw;}
.NavBar{
    position: fixed;
    background-color: #282c34;
    display: flex;
    width: 100%;
    justify-content: space-between;
    padding: 0px;
    box-shadow: 0px 0px 30px 2px black;
    border-bottom: 3px solid black;
    z-index: 5;
}
.NavBar a{
    max-height: 100%;
    padding: 10px 0px;
}
.NavBar a:hover{
    cursor: pointer;
    
    height: 100%;
}
.NavBar nav{
    max-height: 100%;
    
    align-self: center;
}

#igation{
    padding: 0px;
    margin: 0px;
}
#igation a{
    padding: 0rem 1rem;
    height: 100%;
    color: aliceblue;
}
#logo{
    color: aliceblue;
    text-decoration: none;
    font-size: 2rem;
}
nav button{
    background-color: transparent;
    color: aliceblue;
    border: 0px;
    font-size: 1rem;
}
#registerBtn{
    background-color: hsl(0, 74%, 70%);
    height: 100%;
    padding: .7rem;
    margin: 0px .7rem;
}
/**********************************************/