#Ratings{
    position: relative;
    padding: 5rem;
}
#UpvotingContainer{
    display: flex;
    justify-content: space-evenly;
    width: 100vw;
    
}
#CommentSection{
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
}
#CommentSection section{
    display: flex;
}
#CommentSection section div{
    margin: .5rem;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: left;
}
#metaData{
    width: 50vw;
}
#metaData p{
    margin: .5rem;
}
#CommentInput{
    display: flex;
    flex-direction: column;
    align-items: start;
}
#CommentInput textarea{
    width: 60vw;
    height: 5rem;
}
.slideShow img{
    width: 1rem;
}
.slideShow{
    position: absolute;
    top: 47rem;
}
#Right{
    right: 5rem;
}
#Left{
    left: 5rem;
}

#TopRated{
    max-width: 80vw;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
#TopRated div{
    margin: 2rem;
}