#Hero button{
    background-color: transparent;
    color: aliceblue;
    border: 0px;
    font-size: 1rem;
  }
  #Hero button:hover{
    cursor: pointer;
  }
  #Hero {
    background-color: #111;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  #Hero #learn{
    display: inline;
    background-color: rgb(86, 160, 83);
    height: 100%;
    padding: .7rem;
    margin: 0px .7rem;
  }
 