.posterContainer{
    width: 100vw;
    display: flex;
    justify-content: center;
}
.poster{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: max-content;
    max-width: max-content;
}
.poster img{
    max-width: max-content;
}
.poster a{
    background-color: #111;
    width: 100%;
    color: white;
    font-size: 1.2rem;
    padding: .3rem 0px;
}