#Footer{
    display: flex;
    justify-content: space-between;
    padding: .5rem;
}
#Navs a,#Links a{
    max-height: 100%;
    padding: 4px 0px;
}
#Navs a:hover{
    cursor: pointer;
    
    height: 100%;
}
#Logo{
    color: aliceblue;
    text-decoration: none;
    font-size: 2rem;
}
#Navigation{
    display: flex;
    margin-right: 2rem;
}
#Navigation div{
    display: flex;
    align-items: start;
    flex-direction: column;
    margin-left: 10rem;
}
#Navigation p{
    font-weight: bold;
}