#loginForm{
    display: none;
}
.formContainer{
    position: absolute;
    width: 100%;
    top: 20vh;
    height: max-content;
    display: flex;
    justify-content: center;
}
.formContainer form{
    background-color: rgba(37, 42, 44, 99.5%);
    border-radius: 2rem;
    border: 2px solid black;
    padding: 1rem;
    width: fit-content;
    display: flex;
    flex-direction: column;
    align-items: end;
    font-size: 1.2rem;
}
.formContainer form label{
    margin: 1.2rem 0px;
}
.formContainer form button{
    background-color: transparent;
    color: white;
    padding: .3rem .5rem;
    margin: 0rem 1rem;
}